.list {
  width: 320px;
  padding: 10px;
  margin: 0px;
  list-style: circle;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 10px 40px;
  display: block;
}
.item {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  width: inherit;
}
.itemName {
  margin-right: 5px;
}
.itemPhone {
  margin-right: 20px;
}
